* {
  touch-action: manipulation;
}

body {
  box-sizing: border-box;
  color: #fff;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
  -moz-user-select: -moz-none;
  -webkit-user-select: none;
  user-select: none;
  background-color: #222;
  font-family: "Press Start 2P", sans-serif;
}

canvas {
  position: absolute;
}

.text {
  padding: 20px 40px;
}

.gameHolder {
  height: 520px;
  position: relative;
}

.done {
  color: #4a0;
  text-decoration: line-through;
}

.controls {
  justify-content: space-around;
  width: 512px;
  display: flex;
}

.controls > section {
  width: 200px;
  height: 250px;
  position: relative;
}

.controls .input {
  cursor: pointer;
}

.controls .input:active {
  box-shadow: inset 0 0 10px #000a;
}

.cross .input {
  --rotation: 0deg;
  transform-origin: bottom;
  transform: rotate(var(--rotation));
  background: #000;
  border: none;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 60px;
  height: 60px;
  display: block;
  position: absolute;
  top: 10px;
  left: 70px;
}

.cross .input:after {
  content: "";
  border: 30px solid #0000;
  border-top-color: #0b0b0b;
  position: absolute;
  bottom: -60px;
  left: 0;
}

.cross .input.right {
  --rotation: 90deg;
  top: 50px;
  left: 110px;
}

.cross .input.down {
  --rotation: 180deg;
  top: 90px;
}

.cross .input.left {
  --rotation: -90deg;
  top: 50px;
  left: 30px;
}

.cross .square {
  pointer-events: none;
  background-color: #222;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 95px;
  left: 85px;
}

.buttons, .meta {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.buttons .input {
  --size: 60px;
  width: var(--size);
  height: var(--size);
  text-align: center;
  line-height: var(--size);
  background-color: #a44;
  border-radius: 30%;
}

.meta > div {
  text-align: center;
  color: #fff4;
  background-color: #000;
  border-radius: 6px;
  width: 50px;
  height: 20px;
  font-size: 70%;
  line-height: 20px;
  position: relative;
  transform: rotate(-15deg);
}

